import React from 'react'
import { Container, Row, Col } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './wallet.css'

const WalletCoimngSoon = () => (
  <div className="wallet-section">
    <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
    <Container className="py-5  xs-py-1">
      <Row>
        <Col md={4} lg={4}>
          
          <p className="" style={{ color: '#ce38da', fontWeight: 'bold' }}>
            <FormattedMessage
              id="ares.wallet.subtitle"
            />
          </p>
        </Col>
        <Col md={8} lg={8}>
          <div className="landing__code-img">
              <img src="/images/wallet-img.png" className="landing__img landing__img--over-right" alt="" />
          </div>
        </Col>
        </Row>
    </Container>
  </div>
)

export default WalletCoimngSoon



