import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'

const AboutPlasma = () => (
    <div className="Check-Collections-Section">
        <div className="background-container">
            <div className="custom-shape custom-shape-1" />
        </div>
        <div className="container p-3 pt-0 pb-0 main-content">
            <div>
                <Row className="show-grid pt-40 pb-40">
                    <Col  md={6}>
                        <h1 className="display-3 headline mt-90" style={{ color: '#7400a4', marginBottom: '0px', fontWeight: 'bold' }}>
                            <FormattedMessage id="manage.collections.title" />
                        </h1>
                        <p style={{ color: '#7400a4', fontSize: '1rem' }}>
                            <FormattedMessage id="manage.collections.subtitle" />
                        </p>
                        <div className="button-maintain-more">
                            <Button className="button-9">
                                <FormattedMessage id="view.wallet.title" />
                            </Button>
                        </div>
                    </Col>
                    <Col  md={6} className="xs-mt-50">
                        <img src="/images/collections.png" width="100%" height="100%" alt="" />
                    </Col>
                </Row>
            </div>
        </div>
    </div>
)

export default AboutPlasma



