import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import '../style/custom.css'
import config from '../../data/SiteConfig'
import IntlProvider from '../util/intlProvider'
import MainLayout from '../util/mainLayout'
import NavBar from '../components/Navbar'
import WalletComingSoon from '../components/WalletComingSoon'
import ManageCurrencies from '../components/ManageCurrencies'
import CheckCollections from '../components/CheckCollections'
import ConnectDevices from '../components/ConnectDevices'
import ComingAllDevices from '../components/ComingAllDevices'

import FooterArenaPage from '../components/FooterArenaPage/Footer'
 
const Section = ({ children, className }) => <div className={className}>{children}</div>
 
const Main = ({ langKey, messages }) => (
  <IntlProvider langKey={langKey} messages={messages}>
    <div>
      <NavBar langKey={langKey} color="Sara" />
      <MainLayout langKey={langKey} theme="mainwallet" footerTheme="footer">
        <Section>
          <WalletComingSoon/>
        </Section>
        <Section>
          <ManageCurrencies/>
        </Section>
        <Section>
          <CheckCollections/>
        </Section>
        <Section> 
          <ConnectDevices/>
        </Section>
        <Section>
          <ComingAllDevices/>
        </Section>
        <FooterArenaPage config={config} langKey={langKey} />
      </MainLayout>
    </div>
  </IntlProvider>
)
 
export default Main


