import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'

const AboutPlasma = () => (
  <div className="Connect-Devices-Section">
  <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
  <div className="container p-3 pt-0 pb-0 main-content">
    <div>
      <Container>
        <Row className="show-grid pt-40 pb-40">
            <Col  md={6}>
            <img src="/images/devices.png" width="100%" height="100%" alt="" />
            </Col>
            <Col  md={6}>
            <div>
              <h1 className="display-3 headline mt-90" style={{ color: '#ff6427', marginBottom: '0px', fontWeight: 'bold' }}>
                  <FormattedMessage id="connect.devices.title" />
              </h1>
              <p  style={{ color: '#ff6427', fontSize: '1rem' }}>
                <FormattedMessage id="connect.devices.subtitle" />
              </p>
            </div>
            <div className="button-maintain-more">
                <Button className="button-10">
                <FormattedMessage id="view.wallet.title" />
                </Button>
            </div>
            </Col>
        </Row>
    </Container>
    </div>
  </div>
  </div>
)

export default AboutPlasma



