import React from 'react'
import { Container, Row, Col } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './wallet.css'

const comingalldevices = () => (
  <div className="comingalldevices-section">
    <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
    <Container className="">
      <Row>
        <Col md={6} lg={6}>
            <div className="landing__code-img">
                <img src="/images/comingsoon.png" className="landing__img landing__img--over-left" alt="" />
            </div>
          </Col>
          <Col md={6} lg={6} style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
            <h1 className="display-3 headline" style={{ color: '#fff', fontWeight: 'bold' }}>
              <FormattedMessage
                id="Coming.soon.to.iOS"
              /><br/>
              <FormattedMessage
                id="android.and.web"
              />
            </h1>
          </Col>
        </Row>
    </Container>
  </div>
)

export default comingalldevices



