import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'

const AboutPlasma = () => (
  <div className="Manage-Currencies-Section">
  <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
  <div className="container p-3 pt-0 pb-0 main-content">
    <div>
      <Container>
        <Row className="show-grid pt-40 pb-40">
            
            <Col  md={6}>
            <img src="/images/currencies.png" width="100%" height="100%" alt="" />
            </Col>
            <Col  md={6}>
            <h1 className="display-3 headline mt-90" style={{ color: '#001ada', marginBottom: '0px', fontWeight: 'bold' }}>
                <FormattedMessage id="manage.currencies.title" />
                </h1>
            <p style={{ color: '#001ada', fontSize: '1rem' }}>
              <FormattedMessage id="manage.currencies.subtitle1" /><br/>
              <FormattedMessage id="manage.currencies.subtitle2" />
            </p>
            <div className="button-maintain-more">
                <Button className="button-2">
                <FormattedMessage id="view.wallet.title" />
                </Button>
            </div>
            </Col>
        </Row>
    </Container>
    </div>
  </div>
  </div>
)

export default AboutPlasma



